@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&family=Lexend+Deca&display=swap);
body {
  margin: 0;
  font-size: 15px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card-group {
  /* height: 96vh; */
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

@media (min-width: 980px) { /* 980px */
  .card-group {
    height: 96vh;
  }
}

/* Mobile first */
.card{
	width: 231px;
	height: 346px;
	padding: 48px;
}

.card-title {
	margin: 30px 0;
	font-family: "Big Shoulders Display", cursive;
	font-size: 40px;
	text-transform: uppercase;
	color: hsl(0, 0%, 95%);
}

.card-body{
  font-family: "Lexend Deca", sans-serif;
  color: hsla(0, 0%, 100%, 0.75);
  line-height: 25px;
  margin: 0 0 28px 0;
}

.card a {
  border: none;
  font-family: "Lexend Deca", sans-serif;
  background-color: hsl(0, 0%, 95%);
  padding: 15px 35px;
  border-radius: 25px;
  text-decoration: none;
}

.card a:hover{
  background-color: transparent;
  border: 2px solid hsla(0, 0%, 100%, 0.75);
  color: hsla(0, 0%, 100%, 0.75) !important;
}

.card:nth-child(1){
  background-color: hsl(31, 77%, 52%);
  border-radius: 10px 10px 0 0;
  margin-top: 88px;
}

.card:nth-child(1) a {
  color: hsl(31, 77%, 52%);
}

.card:nth-child(2) {
  background-color: hsl(184, 100%, 22%);
}

.card:nth-child(2) a {
  color: hsl(184, 100%, 22%);
}

.card:nth-child(3) {
  background-color: hsl(179, 100%, 13%);
  border-radius: 0 0 10px 10px;
  margin-bottom: 88px;
}

.card:nth-child(3) a {
  color: hsl(179, 100%, 13%);
}

/* Responsive */
@media(min-width: 650px){
  .card{
    margin: auto 25px;
  }
}

@media(min-width: 750px){
  .card{
    margin: auto 50px;
  }
}

@media(min-width: 850px){
  .card:nth-child(1){
    margin: 88px 100px 0;
  }

  .card:nth-child(3){
    margin: 0 100px 88px;
  }

  .card{
    margin: auto 100px;
  }
}

/* Desktop view */
@media(min-width: 980px){
  .card{
    width: 215px;
    height: 408px;
    padding: 46px;
    margin: unset;
  }

  .card-body{
    margin: 0 0 85px 0;
  }

  .card:nth-child(1){
    margin: unset;
    border-radius: 10px 0 0 10px;
  }

  .card:nth-child(3){
    margin: unset;
    border-radius: 0 10px 10px 0;
  }
}
.attribution {
	font-size: 11px;
	text-align: center;
  }
  .attribution a {
	color: hsl(228, 45%, 44%);
  }
